import helper from "../../../common/helper.js";
export default {
	data() {
		return {
			index: -1,
			picker: [
				
				],
			pickerID: [],
			pointsType: '',
			toast: '请选择通知来源',
			dangerRoad: '',
			noticeFrom: '',
			date: '请选择整改截止日期',
			now: '',
			originalData: [],
			dangerInfoRow: [],
			showDangerCheckBox: false,
			choose: '',
			current: -1,
			dangerCheckBox: [],
			place: '',
			dec: '',
			yaoqiu: '',
			switchB: false,
			switchC: false,
			imgList: [],
			commitTime: 0
		}
	},
	methods: {
		PickerChange(e) {
			console.log(e)
			this.index = e.detail.value;
			this.toast = this.picker[this.index];
			for (let s of this.pickerID) {
				if (this.toast == s.deptName) {
					this.pointsType = s.pointsType
				}
			}
			console.log(this.pointsType)
		},
		getFrom() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Get_pointsType',
				data: {
					source: '上级通知'
				}
			}).then(data => {
				var [error,res] = data;
				console.log(res.data);
				if (res.data.code == 0) {
					self.pickerID = res.data.data;
					for (let s of self.pickerID) {
						self.picker.push(s.deptName)
					}
				}
			})
		},
		chooseFrom() {
			
		},
		showMoadel(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {}
				}
			});
		},
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['camera', 'album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '提示',
				content: '确定要删除这张照片？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		SwitchB(e) {
			this.switchB = e.detail.value
			console.log(this.switchB)
		},
		SwitchC(e) {
			this.switchC = e.detail.value
			console.log(this.switchC)
		},
		DateChange(e) {
			this.date = e.detail.value
		},
		reset() {
			this.dangerInfoRow = this.originalData;
			this.dangerCheckBox = [];
			this.showDangerCheckBox = false;
			this.dangerRoad = '';
			this.choose = '';
			this.current = -1;
		},
		getInfo() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Get_AllBZ2',
				data: {
					DWCD: self.$store.state.userInfo.DWCD,
					// RYXH: self.$store.state.userInfo.RYXH
				},
				method: 'GET',
				header: {
					'content-type': 'application/json' // 默认值
				},
				success(res) {
		
					let data = JSON.parse(res.data)
					
					if (res.data.length == 0) {
						self.showMoadel('此处没有隐患源')
					} else {
						self.originalData = data;
						self.dangerInfoRow = data;
					}
				}
			})
		},
		radioChange: function(evt) {
			// this.getPCYD(evt.target.value)
			for (let i = 0; i < this.dangerCheckBox.length; i++) {
				if (this.dangerCheckBox[i].YSCD === evt.target.value) {
					this.current = i;
					this.choose = evt.target.value;
					this.dangerRoad = this.dangerRoad + '->'+this.dangerCheckBox[i].YSMC
					break;
				}
			}
		},
		getNext(item) {
			
			var self = this;
			uni.showLoading({
				title: '加载中',
				mask:true
			})
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Choose_YS3',
				method: 'GET',
				data: {
					id: item.XZCD,
					RYXH: self.$store.state.userInfo.RYXH
				},
				header: {
					'custom-header': 'application/json'
				},
				success: (res) => {
					let data = JSON.parse(res.data);
					
					if (data.length == 0) {
						self.showMoadel('此处没有隐患源')
					} else if (data[0].XZMC) {
						self.dangerInfoRow = data;
						self.dangerRoad = self.dangerRoad + item.XZMC + '->';
					} else {
						self.dangerCheckBox = data;
						self.dangerRoad = self.dangerRoad + item.XZMC;
						self.showDangerCheckBox = true;
					}
					uni.hideLoading()
				}
			});
		},
		commit() {
			console.log('commit')
			var self = this;
			var len = self.imgList.length;
			if (!self.place||!self.dec||!self.proposal||!self.yaoqiu||!self.pointsType) {
				self.showMoadel('请完善隐患信息！');
			} else if (this.date == '请选择整改截止日期') {
				self.showMoadel('请选择整改截止日期!')
			}else if (!self.choose) {
				self.showMoadel('请选择隐患!');
			} else if (self.imgList.length == 0){
				self.showMoadel('请选择隐患照片!');
			} else {
				uni.showLoading({
					title:'提交中。。。',
					mask: true
				})
				self.uploadimg(len)
			}
		},
		uploadimg(len) {
			var self = this;
			var postImgUrl = self.$store.state.urlPrefix + '/Process/Everyone_Sava_images2';
			uni.uploadFile({
				url: postImgUrl,
				filePath: self.imgList[self.commitTime],
				name: 'image',
				formData: {
					RYXH: self.$store.state.userInfo.RYXH,
					YSCD: self.choose,
				},
				success(uploadFileRes) {
					self.commitTime = self.commitTime + 1;
					if (uploadFileRes.data == 'ok') {
						if (self.commitTime<len) {
							self.uploadimg(len)
						} else {
							//commit words
							self.commitTime = 0;
							self.commitSafeWord()
						}
					} else {
						self.commitTime = 0;
						uni.hideLoading();
						uni.showModal({
							title: '提示',
							content: '上报失败！',
							showCancel: false,
							success: function(res) {
								if (res.confirm) {
									uni.reLaunch({
										url: '/pages/basic/home/home'
									})
								}
							}
						});
						
					}
				},
				
			})
		},
		commitSafeWord() {
			var self = this;
				let grade;
				if (self.switchC) {
					grade = 2
				} else {
					grade = 1
				}
				uni.request({
					url: self.$store.state.urlPrefix + '/Home/Add_AbarbeitungInform',
					data: {
						
						EndTime: self.date,
						RYXH: self.$store.state.userInfo.RYXH,
						YSCD: self.choose,
						place: self.place,
						describe: self.dec,
						require: self.yaoqiu,
						opinion: self.proposal,
						isReport: self.switchB,
						grade: grade,
						pointsType: self.pointsType
					},
					method: 'GET',
					header: {
						'content-type': 'application/json' // 默认值
					},
					success(res) {
						uni.hideLoading()
						if (res.data.code == 0){
							uni.showModal({
							    title: '提示',
							    content: '提交成功',
									showCancel: false,
							    success: function (res) {
							        if (res.confirm) {
							            uni.reLaunch({
														url: '/pages/basic/home/home'
													})
							        }
							    }
							});
						} else {
							uni.showModal({
							    title: '提示',
							    content: '提交失败',
									showCancel: false,
							    success: function (res) {
							        if (res.confirm) {
							            uni.reLaunch({
							            	url: '/pages/basic/home/home'
							            })
							        }
							    }
							});
						}
					}
				})
			}
	},
	mounted() {
		this.now = helper.now();
		// this.date = this.now;
		this.getInfo();
		this.getFrom();
	}
}
